@import "./fonts.scss";

$grey1: #333333;
$pink: #f6f0e1;
$white: #ffffff;
$yellow: #f7ca59;
$blue: #a6d7c9;
$bloomarin: #bae8db;

body {
  margin: 0;
}

.container-wrap {
  max-width: 1150px;
  margin: 0 auto;
}

.section_title {
  font: normal 58px Playfair Display;
  line-height: 72px;
  margin-top: 130px;
  text-align: center;
  text-transform: capitalize;
  color: $grey1;

  @media (max-width: 767px) {
    font-size: 1.4em;
  }
}

.welcome {
  position: relative;
  padding-top: 130px;
  background: $pink;
  padding-bottom: 108px;

  .block-wrap {
    justify-content: space-between;

    @media screen and (min-width: 320px) and (max-width: 1145px) {
      justify-content: center;
    }
  }

  .welcome-service {
    overflow: hidden;
    padding-top: 30px;
  }

  h1 {
    font: 60px Playfair Display;
    line-height: 120%;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    margin: 0;

    @media (max-width: 767px) {
      font-size: 26.4px;
    }
  }

  p {
    font: normal 20px niveau-grotesk;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.3px;
    margin-top: 40px;
    color: $grey1;

    @media screen and (max-width: 767px) {
      font-size: 12px;
      margin: 20px auto 0;
      text-align: left;
    }
  }

  .block-wrap {
    position: relative;
    height: auto;
    margin: 33px auto 0;
    z-index: 0;

    .block-content {
      .block-about {
        margin-left: 52px;
        width: 1055px;
        height: 510px;
        background: $white;
        border: 4px solid $grey1;
        box-sizing: border-box;
        border-radius: 20px;
        z-index: 1;

        @media screen and (min-width: 992px) and (max-width: 1145px) {
          width: 1000px;
        }

        @media screen and (min-width: 550px) and (max-width: 1200px) {
          height: auto;
          width: 80%;
          margin: 0 auto;
        }

        @media screen and (min-width: 320px) and (max-width: 550px) {
          height: auto;
          width: 99%;
          margin: 0 auto;
        }

        .block-about_body {
          margin: 0 32px;

          @media screen and (min-width: 320px) and (max-width: 767px) {
            margin: 0 2px;
          }

          .schema_inner {
            position: relative;
            padding-top: 60px;
            @media screen and (min-width: 990px) and (max-width: 1200px) {
              padding: 73px 73px 0;
              height: 505px;
            }
            @media screen and (min-width: 767px) and (max-width: 990px) {
              padding: 40px 20px 0;
              height: 479px;
            }
            @media screen and (min-width: 500px) and (max-width: 767px) {
              padding: 40px 20px 0;
              height: 385px;
              margin-bottom: -45px;
            }
            @media screen and (min-width: 320px) and (max-width: 500px) {
              padding: 40px 0px 0;
              height: 320px;
              margin-bottom: -45px;
            }

            .blockBorder {
              position: absolute;
              top: 55%;
              left: 50%;
              transform: translate(-50%, -50%);
              z-index: 1;
              width: 392px;
              height: 239px;
              border: 4px solid #fcbd3b;

              &::after {
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                width: 0px;
                height: 50px;
                border-right: 4px solid #fcbd3b;
                transform: translate(-50%, -50%);

                @media screen and (min-width: 767px) and (max-width: 990px) {
                  top: 37%;
                }
              }

              @media screen and (min-width: 500px) and (max-width: 767px) {
                top: 49%;
                height: 175px;
                width: 62%;
              }
              @media screen and (min-width: 320px) and (max-width: 500px) {
                top: 55%;
                height: 170px;
                width: 62%;
              }
            }

            .schema_item__big {
              z-index: 2;
              width: 215px;
              height: 176px;
              background: $yellow;
              border-radius: 10px;
              box-shadow: $grey1 6px 6px;
              margin-bottom: 40px;
              position: relative;

              @media screen and (min-width: 500px) and (max-width: 767px) {
                width: 180px;
                height: 130px;
              }

              @media screen and (min-width: 400px) and (max-width: 500px) {
                width: 169px;
                height: 113px;
              }

              @media screen and (min-width: 320px) and (max-width: 400px) {
                width: 139px;
                height: 113px;
              }

              img {
                @media screen and (min-width: 500px) and (max-width: 767px) {
                  transform: scale(0.7);
                }
                @media screen and (min-width: 320px) and (max-width: 500px) {
                  transform: scale(0.7);
                  margin-bottom: -10px;
                }
              }
            }
            .schema_inner__bottom {
              justify-content: space-around;
              .schema_item__small {
                width: 144px;
                height: 176px;
                background: $yellow;
                z-index: 2;
                border-radius: 10px;
                box-shadow: $grey1 6px 6px;
                @media screen and (min-width: 500px) and (max-width: 767px) {
                  width: 105px;
                  height: 114px;
                  box-shadow: $grey1 4px 4px;
                }

                @media screen and (min-width: 320px) and (max-width: 500px) {
                  width: 80px;
                  height: 99px;
                  box-shadow: $grey1 3px 3px;
                }

                h5 {
                  font: bold 14px niveau-grotesk;
                  line-height: 20px;
                  letter-spacing: 0.3px;
                  text-transform: uppercase;
                  text-align: center;
                  margin-top: 15px;
                  @media screen and (min-width: 500px) and (max-width: 767px) {
                    margin-top: -10px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 500px) {
                    margin-top: 5px;
                  }
                }

                .image {
                  img {
                    height: 88px;
                    @media screen and (min-width: 500px) and (max-width: 767px) {
                      transform: scale(0.7);
                    }
                    @media screen and (min-width: 320px) and (max-width: 500px) {
                      transform: scale(0.7);
                      margin-bottom: -10px;
                      height: 70px;
                    }
                  }
                }
              }
            }
          }

          .list_inner {
            position: relative;
            padding-left: 70px;
            @media screen and (min-width: 400px) and (max-width: 1200px) {
              padding-left: 40px;
            }
            @media screen and (min-width: 320px) and (max-width: 400px) {
              padding-left: 4px;
            }
            h3 {
              padding-top: 36px;
              font: 900 34px niveau-grotesk;
              line-height: 48px;
              color: $grey1;

              @media (max-width: 767px) {
                font-size: 22.4px;
              }
            }
            .list {
              margin-top: 32px;
              padding-left: 25px;
              @media screen and (min-width: 320px) and (max-width: 550px) {
                margin-top: 0px;
              }
              .list_item {
                list-style: none;
                margin-bottom: 20px;
                @media screen and (min-width: 320px) and (max-width: 550px) {
                  margin-bottom: 5px;
                }
                position: relative;
                &:nth-child(2),
                &:nth-child(3) {
                  &:after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: -25px;
                    transform: translateY(-50%);
                    width: 20px;
                    height: 3px;
                    background-color: #000000;
                    @media screen and (max-width: 550px) {
                      height: 2px;
                    }
                  }
                }
                .list_img {
                  margin-right: 13px;
                }
                .list_title {
                  font: normal 22px niveau-grotesk;
                  line-height: 32px;
                  letter-spacing: 0.3px;
                  color: $grey1;

                  @media (max-width: 767px) {
                    font-size: 14.04px;
                  }
                }
              }
            }
            .list_line {
              position: absolute;
              top: 146px;
              bottom: 11%;
              left: 16.5%;
              width: 20px;
              border-radius: 7px 0 0 7px;
              border-left: 3px solid #000;
              border-top: 3px solid #000;
              border-bottom: 3px solid #000;

              @media screen and (min-width: 901px) and (max-width: 1200px) {
                left: 39px;
    top: 131px;
    bottom: 9%;
              }
              @media screen and (min-width: 769px) and (max-width: 901px) {
                left: 39px;
                top: 131px;
                bottom: 11%;
              }

              @media screen and (min-width: 550px) and (max-width: 769px) {
                left: 39px;
                top: 130px;
                bottom: 9%;
              }
              @media screen and (min-width: 400px) and (max-width: 550px) {
                left: 39px;
                top: 104px;
                bottom: 11%;
                border-left: 2px solid #000;
                border-top: 2px solid #000;
                border-bottom: 2px solid #000;
              }

              @media screen and (min-width: 358px) and (max-width: 400px) {
                left: 3px;
                top: 107px;
                bottom: 11%;
                border-left: 2px solid #000;
                border-top: 2px solid #000;
                border-bottom: 2px solid #000;
              }
              @media screen and (min-width: 320px) and (max-width: 358px) {
                left: 3px;
                top: 122px;
                bottom: 8%;
                border-left: 2px solid #000;
                border-top: 2px solid #000;
                border-bottom: 2px solid #000;
              }
            }
          }
        }

        &:after {
          content: " ";
          position: absolute;
          right: 0;
          top: 16px;
          z-index: -1;
          width: 1031px;
          height: 510px;
          background: $grey1;
          border-radius: 20px;
          @media screen and (max-width: 1200px) {
            width: 831px;
            right: 14%;
            height: 526px;
          }
          @media screen and (max-width: 1199px) {
            width: 831px;
            right: 14%;
            height: 100%;
          }

          @media screen and (min-width: 550px) and (max-width: 1190px) {
            height: 100%;
            width: 77%;
            margin: 0 auto;
            right: 8%;
          }
          @media screen and (min-width: 320px) and (max-width: 550px) {
            height: 100%;
            width: 99%;
            margin: 0 auto;
            right: -2%;
          }
        }
      }
    }
    .block-marker {
      position: absolute;
      width: 70px;
      height: 244px;
      background: $grey1;
      border-radius: 20px 0px 0px 20px;
      left: 0;
      top: 0;
      z-index: -1;

      @media screen and (min-width: 767px) and (max-width: 1200px) {
        left: 5%;
      }
      @media screen and (min-width: 550px) and (max-width: 767px) {
        left: 3%;
      }
      @media screen and (min-width: 320px) and (max-width: 550px) {
        left: 15%;
      }
      p {
        color: $white;
        white-space: nowrap;
        font: 900 14px niveau-grotesk;
        line-height: 143%;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        margin: 0;
        transform: rotate(-90deg) translateY(-5px);
      }
    }
  }
}

.integration {
  position: relative;
  padding-bottom: 132px;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-bottom: 60px;
  }

  .integration_body {
    position: relative;
    padding-top: 162px;

    @media screen and (min-width: 320px) and (max-width: 767px) {
      padding-top: 60px;
    }

    h2 {
      font: 400 58px Playfair Display;
      color: $grey1;
      margin-bottom: 32px;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        padding: 10px;
        margin: 0 auto 10px;
        line-height: 1.25em;
        font-size: 22.4px;
        text-align: center;
      }

      br {
        @media screen and (min-width: 320px) and (max-width: 767px) {
          display: none;
        }
      }
    }
    .integration_description {
      font: 400 20px niveau-grotesk;
      line-height: 28px;
      letter-spacing: 0.3px;
      color: $grey1;
      margin-bottom: 79px;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        padding: 0px;
        line-height: 1.25em;
        font-size: 14.04px;
        margin: 0 auto 20px;
      }
    }
  }

  .integration_slider {
    margin-left: 80px;
    background: $pink;
    border-radius: 20px;
    height: auto;
    width: 576px;
    @media screen and (min-width: 992px) and (max-width: 1200px) {
      margin-left: 5px;
      width: 550px;
    }

    @media screen and (min-width: 320px) and (max-width: 992px) {
      margin-left: 0px;
      width: 100%;
    }

    .integration_slide {
      position: relative;
      display: none;
      margin: 37px 0 0px;
      @media screen and (min-width: 320px) and (max-width: 767px) {
        margin: 20px auto 50px;
        width: 80%;
        height: auto;
        padding-bottom: 0px;
      }
      .block_technical {
        position: absolute;
        top: -22px;
        left: 55px;
        @media screen and (min-width: 767px) and (max-width: 992px) {
          left: 15px;
        }
        @media screen and (min-width: 320px) and (max-width: 767px) {
          left: 50%;
          transform: translateX(-50%) scale(0.7);
          top: -40px;
        }
      }

      h3 {
        padding: 121px 0 0 64px;
        font: 900 32px niveau-grotesk;
        line-height: 48px;
        text-transform: uppercase;
        color: #000000;
        @media screen and (min-width: 767px) and (max-width: 992px) {
          padding-left: 30px;
        }
        @media screen and (min-width: 320px) and (max-width: 767px) {
          text-align: center;
          padding: 100px 0 0 0;
          line-height: 1.25em;
          font-size: 22.4px;
        }
      }
      .integration_slide__text {
        font: 420 20px niveau-grotesk;
        line-height: 30px;
        letter-spacing: 0.3px;
        color: #000000;
        padding: 0 64px;
        margin: 16px auto 64px;
        @media screen and (min-width: 767px) and (max-width: 992px) {
          padding-left: 30px;
        }
        @media screen and (min-width: 320px) and (max-width: 767px) {
          padding: 0px;
          line-height: 1.25em;
          font-size: 14.04px;
          margin-bottom: 30px;
        }
      }
    }

    .active {
      display: block;
    }

    .integration_slider__indicators {
      width: 100%;
      height: 80px;
      background: #fc6839;
      border-radius: 0px 0px 20px 20px;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        height: 60px;
      }

      .slider__control {
        position: relative;
        cursor: pointer;
        text-decoration: none;
        text-transform: uppercase;
        font: 900 20px niveau-grotesk;
        line-height: 28px;
        letter-spacing: 0.3px;
        color: #ffffff;
        @media screen and (min-width: 320px) and (max-width: 767px) {
          line-height: 20px;
          font-size: 14.04px;
        }
      }
      .slider__control_prev {
        &::before {
          content: "";
          position: absolute;
          width: 48px;
          height: 3px;
          background: #ffffff;
          transform: translate(-50%, -50%);
          top: 50%;
          left: -35px;

          @media screen and (min-width: 320px) and (max-width: 767px) {
            width: 35px;
            height: 2px;
            left: -27px;
          }
        }
      }

      .slider__control_next {
        &::after {
          content: "";
          position: absolute;
          width: 48px;
          height: 3px;
          background: #ffffff;
          transform: translate(-50%, -50%);
          top: 50%;
          right: -85px;

          @media screen and (min-width: 320px) and (max-width: 767px) {
            width: 35px;
            height: 2px;
            right: -60px;
          }
        }
      }
    }
  }
}

.implement {
  position: relative;
  padding-bottom: 136px;
  background-color: $pink;

  @media (max-width: 767px) {
    padding-bottom: 36px;
  }
  h2 {
    font: normal 58px Playfair Display;
    line-height: 72px;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    padding-top: 130px;
    width: 90%;
    margin: 0 auto 64px;

    @media (max-width: 767px) {
      font-size: 22.4px;
      line-height: 30px;
      width: 100%;
      margin: 0 auto 20px;
      padding-top: 70px;
    }
  }

  p {
    font: 400 24px niveau-grotesk;
    line-height: 32px;
    text-align: center;
    letter-spacing: 0.3px;
    color: $grey1;

    @media (max-width: 767px) {
      font-size: 14.04px;
      line-height: 20px;
      text-align: left;
      margin: 0 auto;
    }
  }

  .implement_body {
    position: relative;
    margin-top: 64px;
    @media screen and (min-width: 320px) and (max-width: 767px) {
      margin-top: 20px;
    }
    .caseMenu {
      margin: 30px auto 0;
      @media screen and (min-width: 767px) and (max-width: 1120px) {
        padding-left: 0;
      }
      @media screen and (min-width: 320px) and (max-width: 767px) {
        margin: 30px auto 45px;
        padding-left: 0;
      }
      .caseMenu_item {
        position: relative;
        width: 352px;
        height: 91px;
        list-style: none;
        @media screen and (min-width: 320px) and (max-width: 767px) {
          width: 80%;
          height: 69px;
          margin: 0 auto;
        }

        &:last-child {
          margin-bottom: 0;
        }

        .linkAnimate {
          font: normal 24px niveau-grotesk;
          line-height: 30px;
          letter-spacing: 0.3px;
          color: #333333;
          text-decoration: none;

          @media (max-width: 767px) {
            font-size: 14.04px;
            line-height: 20px;
          }
        }
      }

      .active {
        border-bottom: 2px solid #fc6839;
        border-top: 2px solid #fc6839;
        .linkAnimate {
          font-weight: bold;
        }

        .caseMenu_arrow {
          display: none;
        }
      }
    }

    .modernСarousel {
      width: 100%;
      height: 100%;
      background-color: #ffffff;
      border-radius: 10px;

      .owl-item {
        display: none;

        .owl-schema {
          margin: 55px auto 40px;
          width: 350px;

          @media screen and (min-width: 320px) and (max-width: 767px) {
            width: 95%;
          }
          .rectangle_top {
            width: 197px;
            height: 45px;
            background: #f6f0e1;
            border: 3px solid $grey1;
            border-radius: 10px;
            font: 900 18px niveau-grotesk;
            line-height: 28px;
            letter-spacing: 0.3px;
            text-transform: uppercase;
            color: $grey1;
            @media (max-width: 767px) {
              font-size: 14.04px;
              line-height: 20px;
              width: 170px;
            }
          }

          .rectangle_center {
            margin: 9px 0 15px;
            width: 249px;
            height: 45px;
            background: #f7ca59;
            border: 3px solid $grey1;
            box-sizing: border-box;
            border-radius: 10px;
            font: 900 18px niveau-grotesk;
            line-height: 28px;
            letter-spacing: 0.3px;
            text-transform: uppercase;
            color: $grey1;
            @media (max-width: 767px) {
              font-size: 14.04px;
              line-height: 20px;
              width: 200px;
            }
          }

          .rectangleBlock_down {
            margin-top: 15px;
            width: 100%;

            @media screen and (min-width: 320px) and (max-width: 767px) {
              width: 275px;
            }
            .rectangle {
              width: 101px;
              height: 90px;
              background: #a6d7c9;
              border: 3px solid $grey1;
              box-sizing: border-box;
              border-radius: 10px;

              @media (max-width: 767px) {
                width: 86px;
                height: 79px;
              }

              img {
               max-height: 60px;
              }
            }
          }

          .rectangle__blue {
            width: 192px;
            height: 152px;
            background: #a6d7c9;
            border: 3px solid $grey1;
            box-sizing: border-box;
            border-radius: 10px;
            box-shadow: $grey1 3px 3px;
            @media (max-width: 767px) {
              font-size: 14.04px;
              line-height: 20px;
            }
            &:first-child {
              margin-bottom: 10px;
            }
            &:last-child {
              margin-top: 10px;
            }

            .rectangle_title {
              font: 900 18px niveau-grotesk;
              line-height: 28px;
              letter-spacing: 0.3px;
              text-transform: uppercase;
              color: $grey1;
              margin-bottom: 5px;

              @media (max-width: 767px) {
                font-size: 14.04px;
                line-height: 20px;
              }
            }
          }

          .rectangle__yellow {
            width: 249px;
            height: 45px;
            background: #f7ca59;
            border: 3px solid $grey1;
            box-shadow: $grey1 3px 3px;
            margin: 10px 0;
            box-sizing: border-box;
            border-radius: 10px;
            font: 900 18px niveau-grotesk;
            line-height: 28px;
            text-align: center;
            letter-spacing: 0.3px;
            text-transform: uppercase;
            color: $grey1;

            @media (max-width: 767px) {
              font-size: 14.04px;
              line-height: 20px;
            }
          }
        }

        .owl-title {
          position: relative;
          margin: 43px auto 0;
          font: 900 32px niveau-grotesk;
          line-height: 48px;
          text-align: center;
          letter-spacing: 0.3px;
          text-transform: uppercase;
          color: $grey1;

          @media (max-width: 767px) {
            font-size: 20.4px;
            margin-top: 20px;
            line-height: 30px;
          }
        }

        .owl-description {
          font: normal 20px niveau-grotesk;
          line-height: 30px;
          text-align: left;
          letter-spacing: 0.3px;
          color: $grey1;
          margin: 24px auto 0;
          padding: 0 64px;
          @media screen and (min-width: 767px) and (max-width: 992px) {
            padding: 0 30px;
          }
          @media (max-width: 767px) {
            font-size: 14.04px;
            margin: 20px auto 0;
            line-height: 20px;
            padding: 0 15px;
          }
        }
      }

      .active {
        display: flex;
      }
    }

    .image_poster {
      position: absolute;
      bottom: 0;
      left: 50px;
      @media screen and (min-width: 767px) and (max-width: 992px) {
        padding-left: 0px;
        left: -10px;
        bottom: 7%;
      }

      @media screen and (min-width: 320px) and (max-width: 767px) {
        position: relative;
        left: 0;
        display: flex;
        justify-content: center;
        width: 100%;
        margin-top: -30px;
      }

      .woman_photo {
        position: relative;
        z-index: 2;
        @media screen and (min-width: 767px) and (max-width: 992px) {
          transform: scale(0.8);
          left: 20px;
        }
        @media screen and (min-width: 320px) and (max-width: 767px) {
          transform: scale(0.7);
        }
      }
      .dots {
        position: absolute;
        z-index: 1;
        left: -20%;
        bottom: 10%;

        @media screen and (min-width: 320px) and (max-width: 767px) {
          left: 13%;
        }
      }
    }
  }
}

.connection {
  background-color: $pink;
  padding-bottom: 132px;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-bottom: 62px;
  }
  .connection_body {
    background: #fc6839;
    border-radius: 20px;
    position: relative;
    overflow: hidden;
    padding-bottom: 60px;
    @media screen and (min-width: 767px) and (max-width: 1160px) {
      width: 99%;
      margin: 0 auto;
    }
    @media screen and (min-width: 320px) and (max-width:767px) {
      width: 99%;
      margin: 0 auto;
      padding-bottom: 0;
    }

    h2 {
      font: 900 32px niveau-grotesk;
      line-height: 48px;
      color: #ffffff;
      width: 88%;
      padding: 80px 0 0 80px;
      @media screen and (min-width: 992px) and (max-width: 1000px) {
        padding: 80px 0 0 35px;
      }
      @media screen and (min-width: 767px) and (max-width: 992px) {
        padding: 80px 0 0 0;
        width: 90%;
        margin: 0 auto;
        text-align: center;
      }
      @media (max-width: 767px) {
        font-size: 20.4px;
        line-height: 30px;
        margin: 0 auto;
        width: 90%;
        text-align: center;
        padding: 40px 0 0;
      }
    }

    p {
      font: 500 20px niveau-grotesk;
      line-height: 28px;
      letter-spacing: 0.3px;
      color: #ffffff;
      padding-left: 80px;
      margin: 24px 0 40px;
      width: 85%;
      @media screen and (min-width:992px) and (max-width: 1000px) {
        padding-left: 35px;
      }
      @media screen and (min-width:767px) and (max-width: 992px) {
        padding-left: 35px;
        width: 53%;
      }
      @media screen and (min-width: 500px) and (max-width: 767px) {
        font-size: 14.04px;
        margin-top: 20px;
        line-height: 20px;
        padding-left: 35px;
        width: 100%;
        margin: 24px 0 30px;
      }
      @media screen and (min-width: 320px) and (max-width: 500px) {
        font-size: 14.04px;
        margin-top: 20px;
        line-height: 20px;
        padding-left: 15px;
        width: 100%;
        margin: 24px 0 30px;
      }
    }

    a {
      text-decoration: none;
      cursor: pointer;
      padding: 8px 85px;
      box-shadow: 6px 6px #333;
      background: #ffffff;
      border: 4px solid $grey1;
      border-radius: 10px;
      font: 900 18px niveau-grotesk;
      line-height: 30px;
      text-align: center;
      text-transform: uppercase;
      color: $grey1;
      margin-left: 80px;

      @media screen and (min-width: 767px) and (max-width: 1000px) {
        margin-left: 35px;
      }
      @media screen and (min-width: 500px) and (max-width: 767px) {
        font-size: 14.04px;
        margin-top: 20px;
        line-height: 20px;
        box-shadow: 3px 3px #333;
        padding: 8px 60px;
        margin-left: 35px;
      }

      @media screen and (min-width: 320px) and (max-width: 500px) {
        font-size: 14.04px;
        margin-top: 20px;
        line-height: 20px;
        box-shadow: 1px 1px #333;
        padding: 8px 40px;
        margin-left: 15px;
      }
    }

    .image {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 45%;

      @media screen and (min-width: 768px) and (max-width: 992px) {
        
      width: 40%;
      }

      @media screen and (min-width: 500px) and (max-width: 768px) {
        position: relative;
        margin-left: auto;
      }

      @media screen and (min-width: 320px) and (max-width: 500px) {
        position: relative;
        margin-left: auto;
        width: 65%;
      }
    }
  }
}

.protocols {
  background-color: $blue;
  padding-bottom: 143px;
  h2 {
    padding-top: 132px;
    margin-top: 0;

    @media screen and (min-width: 320px) and (max-width: 767px) {
      padding-top: 62px;
    }
  }
  .section_description {
    font: 420 24px niveau-grotesk;
    line-height: 28px;
    letter-spacing: 0.3px;
    color: $grey1;
    text-align: center;
    margin: 40px auto 74px;

    @media screen and (max-width: 767px) {
      line-height: 20px;
      font-size: 14.04px;
      margin: 10px auto 45px;
    }
  }

  .protocols_body {
    width: 100%;

    @media screen and (min-width: 320px) and (max-width: 500px) {
      flex-direction: column;
    }
    .protocols_item {
      position: relative;
      width: 20%;
      height: 138px;
      border-bottom: 1px solid #ffffff;
      border-right: 1px solid #ffffff;

      @media screen and (min-width: 500px) and (max-width: 767px) {
        height: 100px;
      }

      @media screen and (min-width: 320px) and (max-width: 500px) {
        width: 80%;
        border-right: none;
        margin: 0 auto;
        height: 85px;
      }

      &:last-child {
        border-right: none;
      }

      .protocols_code {
        font: 300 20px Fira Code;
        line-height: 32px;
        color: #ffffff;
        position: absolute;
        left: 16px;
        top: 0;

        @media screen and (min-width: 500px) and (max-width: 767px) {
          line-height: 20px;
          font-size: 14.04px;
        }

        @media screen and (min-width: 320px) and (max-width: 500px) {
          top: 11px;
          line-height: 20px;
          font-size: 14.04px;
        }
      }
      h4 {
        font: 900 24px niveau-grotesk;
        line-height: 28px;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;

        @media screen and (max-width: 767px) {
          line-height: 20px;
          font-size: 14.04px;
        }
      }
    }
  }
}

